body,
html {
  background-color: rgb(6, 171, 177);
  margin: 0;
  font-family: "Segoe UI", "Roboto";
}
.stopwatch {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 30px;
}
.tabl {
  padding-top: 20px;
  margin-bottom: 15px;
  height: 50px;
  width: 150px;
  text-align: center;
  font-size: 25px;
  font-weight: 700;
  border: 2px solid rgb(221, 255, 71);
  border-radius: 10%;
  box-shadow: 0 0 5px 5px rgba(0, 0, 0, 0.4);
  background-color: tomato;
}
.btn {
  width: 75px;
  height: 30px;

  padding: 5px;
  margin-right: 10px;
  margin-bottom: 5px;

  font-size: 18px;
  font-weight: 600;
  box-shadow: 5px 5px 5px 2px rgba(34, 60, 80, 0.4);
}
.btn:hover {
  background-color: teal;
  color: antiquewhite;
  box-shadow: 1px 1px 1px 2px rgba(34, 60, 80, 0.4);
}
